.course_box {
  width: 100%;
  height: 213px;
  background-color: #f1f2f3;
  border-radius: 16px;
  overflow: hidden;

  .cover {
    height: 88px;
    //overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: fill;
    }
  }

  .down {
    height: calc(100% - 108px);
    padding: 8px 12px 12px 12px;
    background-color: #fff;

    .title {
      height: 19px;
      font-family: HarmonyHeiTi;
      font-size: 14px;
      color: rgba(0, 0, 0, 0.90);
      text-align: left;
      font-weight: 400;
      margin-bottom: 2px;
      line-height: 19px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis; // clip设置属性为clip不显示点点点；
      -o-text-overflow: ellipsis; // clip设置属性为clip不显示点点点；
    }

    .brief {
      height: 32px;
      line-height: 16px;
      font-family: HarmonyHeiTi;
      font-size: 12px;
      color: rgba(0, 0, 0, 0.60);
      text-align: left;
      font-weight: 400;
      overflow: hidden;
      text-overflow: ellipsis; // clip设置属性为clip不显示点点点；
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      margin-bottom: 12px;
    }

    .other_info {
      .info_line {
        height: 16px;
        text-align: left;
        line-height: 16px;
        display: flex;
        justify-content: flex-start;
        align-items: center;

        .tips {
          font-family: HarmonyHeiTi-Light;
          font-size: 12px;
          color: #000000;
          text-align: left;
          font-weight: 200;
        }

        &:nth-child(1) {
          margin-bottom: 8px;
        }

        img {
          width: 16px;
          height: 16px;
          object-fit: contain;
          margin-right: 4px;
        }
      }
    }
  }
}

.ant-row > .ant-col:last-child {
  padding-right: 0;

}