//$padding: 16px;
$SM_SIZE: 320px;
$MD_SIZE: 600px;
$LG_SIZE: 1000px;
$XL_SIZE: 1500px;
:root {
  --containerPadding: 0;
  --bannerHeight: 168px;
  --innerBoxTop: 0px;
  --innerBoxLeft: 0px;
}

@media screen and (min-width: $SM_SIZE) and (max-width: $MD_SIZE) {
  //  sm时
  :root {
    --containerPadding: 16px;
  }
}

@media screen and (min-width: $MD_SIZE) and (max-width: $LG_SIZE) {
  //  md时
  :root {
    --bannerHeight: 168px;
    --containerPadding: 24px;
  }
}

@media screen and (min-width: $LG_SIZE) and (max-width: $XL_SIZE) {
  // lg时
  :root {
    --bannerHeight: 230px;
    --innerBoxTop: 54px;
    --innerBoxLeft: 94px;
    --containerPadding: 32px;
  }
}

@media screen and (min-width: $XL_SIZE) {
  //xl时
  :root {
    --bannerHeight: 372px;
    --innerBoxTop: 109px;
    --innerBoxLeft: 117px;
    --containerPadding: 40px;
  }
}


.course_learing_box {
  padding: 0 var(--containerPadding);

  .complexBanner {
    width: 100%;
    height: var(--bannerHeight);
    background-color: #E7E9EC;
    border-radius: 16px;
    overflow: hidden;
    position: relative;
    transition: all ease-in-out .3s;

    .inner_banner {
      height: 100%;
      text-align: right;

      img {
        height: 100%;
      }
    }

    .inner_description {
      position: absolute;
      top: var(--innerBoxTop);
      left: var(--innerBoxLeft);

      .innner_page_title {
        font-family: HarmonyHeiTi-Bold;
        font-size: 36px;
        color: #000000;
        text-align: left;
        line-height: 33px;
        font-weight: 700;
        margin-bottom: 18px;
      }

      .inner_brief {
        width: 522px;
        height: 72px;
        font-family: HarmonyHeiTi;
        font-size: 18px;
        color: rgba(0, 0, 0, 0.60);
        text-align: left;
        font-weight: 400;
      }
    }
  }


  .page_title {
    font-family: HarmonyHeiTi-Bold;
    font-size: 24px;
    color: #000000;
    text-align: left;
    line-height: 33px;
    font-weight: 700;
  }

  .banner {
    width: 100%;
    height: 168px;
    background-color: #e7e9ec;
    border-radius: 16px;
    margin-top: 19px;
    overflow: hidden;

    img {
      //width: 100%;
      height: 100%;
      //object-fit: cover;
    }
  }

  .description {
    width: 100%;
    text-align: left;
    margin-top: 8px;
    //margin-bottom: 18px;
    font-family: HarmonyHeiTi;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.60);
    //text-align: left;
    font-weight: 400;
  }

  .basic_course {
    font-family: HarmonyHeiTi-Medium;
    font-size: 16px;
    color: #182431;
    text-align: left;
    line-height: 22px;
    font-weight: 500;
    margin-top: 18px;
    margin-bottom: 8.5px;
  }

  .course_box_container {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
  }
}